.App {
  text-align: center;
  display: grid;
  width: 300px;
  margin: auto;
  grid-template-columns: repeat(12, 1fr);
  grid-column-gap: 10px;
  grid-row-gap: 30px;
}

.button {
  background: linear-gradient(to bottom right, #ccf, #aac);
  padding: 10px;
}

.button:nth-child(odd) {
  background: linear-gradient(to bottom right, #fcf, #fac);
  grid-column: 1/6;
}

.button:nth-child(even) {
  background: linear-gradient(to bottom right, #cff, #afc);
  grid-column: 8/13;
}
